export default function VideoBlock() {
  init()
}

function init() {
  // Inject SVG sprite if not already present
  if (!document.getElementById('plyr-svg-sprite')) {
    const div = document.createElement('div');
    div.innerHTML = plyrSvgSprite;
    div.firstChild.id = 'plyr-svg-sprite';
    document.body.appendChild(div.firstChild);
  }

  $('.video-block').each(function() {
    var $playIcon = $(this).find('.play-icon'),
        $overlay = $(this).find('.video-block-video__overlay'),
        $video = $(this).find('video');

    var player = new Plyr($video[0], {
      controls: ['play', 'progress', 'current-time', 'mute', 'volume', 'fullscreen'],
      hideControls: false,
      keyboard: { focused: true, global: false },
    });

    $playIcon.on('click', function() {
      player.play();
      player.media.focus();
    });

    player.on('play', function() {
      $overlay.fadeOut(250);
    });
  });  
}